body {
-webkit-overflow-scrolling: auto ;
}


.header-color {
    background: #2b2b2d; 
    background: -webkit-linear-gradient(to right, #387490, #07767c, #2b2b2d);
    background: linear-gradient(to right, #387490, #01acb6, #2b2b2d);
    position: fixed;
  }



  .landing-grid {
    background: black;  
    background: -webkit-linear-gradient(to right, #387490, #07767c, #2b2b2d);  
    background: linear-gradient(to right, #387490, #01acb6, #2b2b2d); 
    text-align: center;
    position: fixed;
 
    top: 10%;
    left: 0;
    width: 100%;
    height: 100%;
    
  }

  .avatar-img {
    width: 100%;
    max-width: 300px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
    border-color: black;
    border-width: 5px;
    border-style: solid;
    border-radius: 25%;
  
  }
  
  .banner-text {
    background-color: black;
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
  }
  
  .banner-text h1 {
    font-size: 66px;
    font-weight: bold;
    color: white;
  }
  
  .banner-text hr {
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
  }
  
  .banner-text p {
    color: white;
    font-size: 20px;
    padding: 1em;
  }
  
  .social-links {
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto;
  }
  
  .social-links i {
    color: white;
    font-size: 5em;
    padding: 8px;
  }

    

  
.about {

    color: #fff;
    font-size: 100px;
  }

.projects {

    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    color: white;
    font-size: 100px;
  }

  .card-text {
    text-decoration-line: underline;
    color: black;
  } 
  
.resume {
   
    background-position: center;
    background-size: fill;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 100px;
  }

  
